.cv-card {
    max-width: 350px;
    margin: 20px auto;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    background: linear-gradient(135deg, #007BFF, #0056b3); /* Degradado de azul */
    text-align: center;
    color: white; /* Color del texto dentro del card */
  }
  
  .cv-container {
    font-family: Arial, sans-serif;
  }
  
  .cv-image {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
    margin-bottom: 15px;
    border: 4px solid white; /* Borde blanco para destacar la imagen */
  }
  
  h1 {
    font-size: 22px;
    color: white;
    margin-bottom: 10px;
  }
  
  p {
    font-size: 16px;
    color: white;
  }
  
  a {
    text-decoration: none;
    color: white; /* Amarillo para contraste */
    font-weight: bold;
  }
  
  a:hover {
    color: #FFC107;
  }
  